import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
	state: {
		postsText: '',
		posts: [
			'First pages',
			'Second pages',
			'Three pages'
		]
	},
	mutations: {
		remove(state, index) {
			state.posts.splice(index, 1)
		},
		new(state, postsText) {
			state.posts.push(postsText)
		}
	}
});


